/*
 * Rokkitt
 */

/* rokkitt-700 - latin */
@font-face {
  font-family: 'Rokkitt';
  font-style: normal;
  /* font-weight: 700; */
  font-weight: normal;
  src: local(''),
    url('../assets/fonts/Rokkitt/rokkitt-v18-latin-700.woff2') format('woff2'),
    /* Super Modern Browsers */
      url('../assets/fonts/Rokkitt/rokkitt-v18-latin-700.woff') format('woff'),
    /* Modern Browsers */
      url('../assets/fonts/Rokkitt/rokkitt-v18-latin-700.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('../assets/fonts/Rokkitt/rokkitt-v18-latin-700.svg#Rokkitt')
      format('svg'); /* Legacy iOS */
}

